.row {
  display: flex;
  justify-content: center;
}

h1 {
  font-size: 50px;
  text-align: center;
}

.cell {
  height: 160px;
  width: 160px;
  border: 3px solid black;
  background-color: #bada55;

}

.x {
  background: url(../../images/x.png);
  background-repeat: no-repeat;
  background-position: center;
}

.o {
  background: url(../../images/o.png);
  background-repeat: no-repeat;
  background-position: center;
}

.blue {
  background-color: blue;
}

.container {
  margin: 0 auto;
  text-align: center;
}
.reset {
  display: block;
  width: 50%;
  height: 50px;
  border: 3px solid black;
  border-radius: 5px;
  margin: 5px auto;

}
